	module.exports = {"data":{"markdownRemark":{"html":"<p>For a while I have been playing out at restaurants and breweries.\nThese shows are great because the venue will pay you, and there is no tickets to sell to your fans.\nThe problem I noticed is I do not acquire many new fans in these situations.</p>\n<p>This carried my thoughts to an idea presented by Malcolm Gladwell in his book: The Tipping Point.\nI have a hunch that people are too saturated with new great music these days, and they rely on filters to receive it.\nGladwell calls these entities Connectors.</p>\n<p>Who and What are the connectors are unknown to me.\nI only know my own outlets.\nThis is a great hypothesis to test.\nI would like to survey potential fans about where they find music.</p>\n<p>Here is the survey I would like to conduct:</p>\n<p>Name the last musical artist I have a hunch that people are too saturated with new great music these days, and they rely on filters to receive it.\nGladwell calls these entities Connectors.</p>\n<p>Who and What are the connectors are unknown to me.\nI only know my own outlets.</p>\n<p>I am going to conduct the following survey:</p>\n<ul>\n<li>What is the last song you remember falling in love with (to the best of your knowledge):</li>\n<li>Where did you first hear that song (to the best of your knowledge):</li>\n</ul>\n<p>Hopefully this will shed some light into where people discover music.</p>","frontmatter":{"title":"What Are The Fan Connectors?","hero":null},"fields":{"date":"2016-10-07"}}},"pathContext":{"url":"/posts/2016-10-07-fan-connectors/"}}

